html,
body {
  margin: 0;
  padding: 0;
}

.page {
  height: 95vh;
  min-height: 95vh;
}

.draggable {
  position: absolute;
  width: 400px;
  height: 100px;
  background-color: #ffff007a;
}

.clonable {
  padding: 5px;
